<template>
	<v-navigation-drawer :value="value" fixed right width="100%" hide-overlay touchless :height="UI.height.value">
		<v-card class="d-flex flex-column" height="100%">
			<!-- toolbar -->
			<div>
				<v-toolbar dark style="flex: auto">
					<v-btn icon dark @click="close_dialog">
						<v-icon>chevron_left</v-icon>
					</v-btn>
					<v-avatar class="ml-2">
						<img :src="get_contact_avatar()"/>
					</v-avatar>
					<v-toolbar-title>
						<v-list-item two-line>
							<v-list-item-content>
								<v-list-item-title class="text-h6">{{ name }}</v-list-item-title>
								<v-list-item-subtitle class="subtitle-text" v-if="!blocked">
									<v-fade-transition>
										<span class="top-left-corner" v-if="typing" key="1">{{ UI.status_typing }}</span>
										<span class="top-left-corner" v-else key="2">{{ UI.status_online }}</span>
									</v-fade-transition>
								</v-list-item-subtitle>
							</v-list-item-content>
						</v-list-item>
					</v-toolbar-title>
					<v-spacer></v-spacer>
					<v-menu bottom left>
						<template v-slot:activator="{ on, attrs }">
							<v-btn dark icon v-bind="attrs" v-on="on">
								<v-icon>more_vert</v-icon>
							</v-btn>
						</template>
						<v-list>
							<v-list-item link @click="exit(true)">
								<v-list-item-title>{{ UI.clear_conversation }}</v-list-item-title>
							</v-list-item>
						</v-list>
					</v-menu>
				</v-toolbar>
			</div>
			<!-- conversation list -->
			<v-card-text class="flex-grow-1" style="position: relative">
				<div style="position: absolute; top: 10px; left: 0; width: 100%; height: 100%; overflow-y: auto" v-if="!blocked">
					<!-- message -->
					<v-fade-transition group>
						<v-list-item dense v-for="message in conversation" :key="message.id">
							<v-list-item-content :id="message.id" style="margin-bottom: 10px">
								<!-- text -->
								<v-list-item :class="get_list_class(message)">
									<v-card flat dark :color="get_bubble_color(message)" style="border-radius: 15px"
											max-width="70vw">
										<v-card-text class="body-1 py-2" style="color: white; white-space: pre-line">
											{{ message.text }}
										</v-card-text>
										<v-card-actions class="pt-0 px-4 justify-end caption"><span
											class="grey--text text--lighten-2">{{ message.time }}</span>
										</v-card-actions>
									</v-card>
								</v-list-item>
								<!-- chips -->
								<v-list-item :class="get_list_class(message)" v-if="message.chips.length > 0">
									<v-chip-group dark column>
										<v-chip color="grey" :class="get_list_class(message)"
												v-for="chip in message.chips" :key="chip"
												@click="click_chip(chip, message)">
											{{ chip }}
										</v-chip>
									</v-chip-group>
								</v-list-item>
							</v-list-item-content>
						</v-list-item>
					</v-fade-transition>
					<!-- typing -->
					<v-fade-transition hide-on-leave>
						<v-list-item dense v-if="typing">
							<v-list-item-content>
								<v-list-item>
									<v-card flat dark color="grey darken-3" style="border-radius: 15px">
										<v-card-text class="body-1 py-2" style="color: white;">
											<v-icon color="white">more_horiz</v-icon>
										</v-card-text>
									</v-card>
								</v-list-item>
							</v-list-item-content>
						</v-list-item>
					</v-fade-transition>
				</div>
				<div class="text-center" v-else>
					<v-chip>{{ UI.blocked }}</v-chip>
				</div>
			</v-card-text>
			<!-- text bar -->
			<div>
				<v-toolbar dark flat bottom>
					<div class="d-flex flex-grow-1 align-center" style="gap: 15px">
						<div class="flex-grow-1">
							<v-text-field v-model="text" autocomplete="off" filled rounded dense hide-details @keypress.enter="send(text)" autofocus :disabled="blocked"></v-text-field>
						</div>
						<v-btn icon elevation="0" @click="send(text)" :disabled="!text || blocked">
							<v-icon color="primary">send</v-icon>
						</v-btn>
					</div>
				</v-toolbar>
			</div>
		</v-card>
	</v-navigation-drawer>
</template>

<script>
import moment from 'moment';
import $ from 'jquery';
import {api_url, height, conversation} from "@/store/Global";

export default {
	name: "bd-navigation-drawer",
	props: {
		value: Boolean,
		name: String,
		avatar: String,
		blocked: Boolean,
	},
	model: {
		prop: 'value',
		event: 'close',
	},
	data: () => {
		return {
			UI: {
				status_online: '在線上',
				status_typing: '輸入中...',
				clear_conversation: '清除對話',
				blocked: '對方不在線上，不能發送訊息。',
				height: height,
			},
			text: '',
			typing: false,
			conversation: conversation.list,
			buddha_queue: [],
			question: null,
			confirm: false,
			answer: {
				yes: ['是', '對', '係', '啱', '想', '好', '嗯', 'yes', 'yea', 'ya', 'yeah', 'ok'],
				no: ['否', '不', '唔', '錯', 'no', 'nah', 'not'],
			},
			four_noble_truths: [
				{chinese: '苦諦', english: 'suffering', delay: 2000},
				{chinese: '集諦', english: 'origin', delay: 2000},
				{chinese: '滅諦', english: 'ending', delay: 2000},
				{chinese: '道諦', english: 'path', delay: 3000},
			],
			noble_eightfold_path: [
				{chinese: '正見', english: 'right_view', delay: 2000},
				{chinese: '正思', english: 'right_resolve', delay: 2000},
				{chinese: '正語', english: 'right_speech', delay: 2000},
				{chinese: '正業', english: 'right_action', delay: 2000},
				{chinese: '正命', english: 'right_livelihood', delay: 2000},
				{chinese: '正勤', english: 'right_effort', delay: 2000},
				{chinese: '正念', english: 'right_mindfulness', delay: 2000},
				{chinese: '正定', english: 'right_concentration', delay: 2000},
			],
			self_words: ['我', '識', '覺', '靈', '魂'],
			anatta: [
				{chinese: '五藴', english: 'skandha', delay: 2000},
				{chinese: '無我', english: 'detail', delay: 2000},
			],
			modern_practice_words: ['家庭', '社會', '關係'],
			practice: [
				{chinese: '現生得樂之道', english: 'happy', delay: 1000},
				{chinese: '身後四德', english: 'virtue', delay: 1000},
			],
			confirm_questions: [
				{question: 'confirm_four_noble_truths', action: 'tell_four_noble_truths'},
				{question: 'confirm_anatta', action: 'tell_anatta_summary'},
				{question: 'confirm_family', action: 'tell_practice_summary'},
				{question: 'confirm_happy', action: 'tell_practice_summary'},
				{question: 'confirm_virtue', action: 'tell_practice_summary'},
				{question: 'confirm_study', action: 'tell_study_detail'},
			],
		}
	},
	watch: {
		value: async function (new_value) {
			if (new_value) {
				// 開啟時如果不是封鎖狀態, 又沒有對話的話就自我介紹
				if (this.conversation.length < 1 && !this.blocked) {
					return this.greetings();
				}
			}
		},
		buddha_queue: async function (new_value) {
			if (new_value && new_value.length > 0) {
				let message = new_value.shift();
				await this.push_conversation(message);
				return this.waiting(message.delay);
			}
		},
	},
	methods: {
		get_contact_avatar() {
			if(this.avatar !== ''){
				return require(`@/assets/${this.avatar}-icon.png`);
			}
		},
		close_dialog() {
			this.$emit('close', false);
		},
		exit(restart = false) {
			this.conversation = [];
			this.question = null;
			this.confirm = false;

			return restart ? this.greetings() : this.close_dialog();
		},
		get_list_class(message) {
			return message.buddha ? '' : 'justify-end';
		},
		get_bubble_color(message) {
			return message.buddha ? 'grey darken-3' : 'primary';
		},
		_uuid() {
			let d = Date.now();
			if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
				d += performance.now(); //use high-precision timer if available
			}
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = (d + Math.random() * 16) % 16 | 0;
				d = Math.floor(d / 16);
				return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
			});
		},
		async waiting(ms) {
			return new Promise(resolve => {
				setTimeout(function () {
					resolve();
				}, ms);
			})
		},
		async show_typing_effect(delay, typing) {
			await this.waiting(delay);
			this.typing = true;
			await this.waiting(typing);
			this.typing = false;
		},
		async click_chip(chip, message) {
			message.chips = [];
			if (message.train_text) {
				await this.train_text(message.train_text, (chip === '我無唔開心呀' ? 'pos' : 'neg'));
			}
			if (chip === '我無唔開心呀') {
				message.text = '我會記住呢個答案。';
				return this.ask_for_confirm('four_noble_truths');
			} else {
				await this.send(chip);
			}
		},
		async greetings() {
			// 自我介紹
			await this.push_new_message('眾生皆苦，我佛慈悲。', true, [], 500, 500);
			// 邀請用戶提問
			await this.push_new_message('請提出你嘅問題，我會盡量解答。', true, [], 1500, 1000);
		},
		async send(text) {
			// 清空輸入欄的文字
			this.text = '';
			if (text) {
				// 發送用戶對話
				await this.push_new_message(text);
				// 發送回應
				await this.response(text);
			}
		},
		async response(text) {
			let ask_for_detail = false;
			// 當輸入'四聖諦'，則提供簡介
			if (text === '四聖諦') {
				return this.tell_four_noble_truths();
			}

			// 如問題是確認八正道但答案又是返回的話，返回聽取其他諦
			if (this.question === 'noble_eightfold_path' && text === '返回') {
				return this.know_more_about_other_truths();
			}

			// 當輸入'苦諦'、'集諦'、'滅諦'、'道諦'，會提供詳細說明
			this.four_noble_truths.forEach(e => {
				if (text === e.chinese) {
					this.question = e.chinese;
					ask_for_detail = true;
					return this.tell_four_noble_truths_detail(e);
				}
			});

			// 當輸入'正見'、'正思'、'正語'、'正業'、'正命'、'正勤'、'正念'、'正定'，會提供詳細說明
			this.noble_eightfold_path.forEach(e => {
				if (text === e.chinese) {
					this.question = e.chinese;
					ask_for_detail = true;
					return this.tell_noble_eightfold_path_detail(e);
				}
			});

			// 當輸入'五藴'、'無我論'，會提供詳細說明
			this.anatta.forEach(e => {
				if (text === e.chinese) {
					this.question = e.chinese;
					ask_for_detail = true;
					return this.tell_anatta_detail(e);
				}
			});

			// 當輸入'五藴'、'無我論'，會提供詳細說明
			this.practice.forEach(e => {
				if (text === e.chinese) {
					this.question = e.chinese;
					ask_for_detail = true;
					return this.tell_practice_detail(e);
				}
			});

			// 如問題是無我或家庭社會關係，但輸入'無興趣'，詢問四聖諦
			if ((this.question === 'anatta' || this.question === 'practice') && text === '無興趣') {
				ask_for_detail = true;
				return this.ask_for_confirm('four_noble_truths');
			}

			// 處理完問題強制退出function
			if (ask_for_detail) {
				return;
			}

			// 如當前沒有任何問題，提出問題
			if (!this.question) {
				await this.push_new_message(`你係咪想問：「${text}」呢？`, true, ['係', '唔係'], 1000, 1000);
				this.question = text;
				return this.confirm = true;
			}

			// 有問題但又不用確認，重新來過
			if (!this.confirm) {
				return this.ask_again();
			}

			// -----下面係有問題又需要確認-----
			let handled = false;

			// 問題是確認聽唔聽四聖諦、無我、家庭社會關係、現生得樂之道、身後四德、修習
			this.confirm_questions.forEach(cq => {
				if (this.question === cq.question) {
					this.confirm = false;
					handled = true;
					if (!this.answer.yes.includes(text.toLowerCase())) {
						return this.ask_again();
					}
					return this[cq.action]();
				}
			});

			// 確認完問題強制退出function
			if (handled) {
				return;
			}

			// 如其他問題答否，重新來過
			if (!this.answer.yes.includes(text.toLowerCase())) {
				return this.ask_again(true);
			}

			// 檢查現代實踐
			if (this.check_contain_modern_practice_words()) {
				return this.ask_for_confirm('family');
			}

			// 檢查修習
			if (this.question.match('修習')) {
				return this.tell_study_summary();
			}

			// 檢查情緒，如果負面則介紹四聖諦
			if (await this.check_bad_mood()) {
				return this.ask_for_confirm('four_noble_truths', true);
			}

			// 最後檢查無我
			return this.check_contain_self_words();
		},
		async check_bad_mood() {
			// 檢查問題的極性來判斷是否心情差
			try{
				let response = await $.post(`${api_url}/emotion`, {
					text: this.question
				});
				console.log(response);
				return response.bad_mood;
			} catch (e){
				console.log('檢查失敗，隨機回傳結果...');
			}

			// 如檢查失敗，隨機回傳結果
			let result = Math.floor(Math.random() * 2);
			console.log(result === 0 ? '正面' : '負面');
			return result;
		},
		async ask_again(uncertain = false, fresh = true) {
			if (fresh) {
				this.question = null;
				this.confirm = false;
			}
			// 如有'不確定'的參數就彈唔明講咩
			if (uncertain) {
				await this.push_new_message('唔好意思，我唔係好明白你想講咩。', true, [], 500, 500);
			}
			await this.push_new_message((uncertain ? '請重複你嘅問題。' : '請提出另一個問題。'), true, [], 1500, 1000);
		},
		async ask_for_confirm(type, sad = false) {
			let message = '';
			let train_text = this.question;
			switch(type) {
				case 'anatta':
					message = '你想唔想了解一下無我嘅簡介呢？'; this.question = 'confirm_anatta'; break;
				case 'four_noble_truths':
					message = sad ? '你似乎有啲唔係幾開心喎，不如我地一齊了解下四聖諦好無？' : '咁你想唔想了解一下四聖諦呢？'; this.question = 'confirm_four_noble_truths'; break;
				case 'family':
					message = '你想唔想了解一下佛教對於家庭社會關係嘅睇法呢？'; this.question = 'confirm_family'; break;
				case 'happy':
					message = '你想唔想了解一下現生得樂之道呢？'; this.question = 'confirm_happy'; break;
				case 'virtue':
					message = '你想唔想了解一下身後四德呢？'; this.question = 'confirm_virtue'; break;
				case 'study':
					message = '你想唔想再了解多啲修習嘅詳細內容呢？'; this.question = 'confirm_study'; break;
			}
			let chips = ['好', '唔好'];
			if (sad) {
				chips.push('我無唔開心呀');
			} else {
				train_text = null;
			}
			await this.push_new_message(message, true, chips, 500, 1000, train_text);
			return this.confirm = true;
		},
		async tell_four_noble_truths() {
			let tell_four_noble_truths_summary = '';
			// 從txt檔案抽出四聖諦各自的簡介並組合成一句
			for (let e of this.four_noble_truths) {
				let summary = require(`../assets/four_noble_truths/${e.english}/summary.txt`).default;
				tell_four_noble_truths_summary += summary + (this.four_noble_truths.indexOf(e) !== (this.four_noble_truths.length - 1) ? "\n\n" : '');
			}
			await this.push_new_message(tell_four_noble_truths_summary, true, [], 1000, 2000);
			// 詢問係咪仲想了解其他諦
			return this.know_more_about_other_truths();
		},
		async tell_four_noble_truths_detail(truth) {
			// 從txt檔案抽出苦諦、集諦、滅諦或道諦的詳細內容
			if (truth.english === 'path') {
				// 如道諦，分開3個對話欄
				for (let e of [1, 2, 3]) {
					let detail = require(`../assets/four_noble_truths/path/detail${e}.txt`);
					await this.push_new_message(detail.default, true, [], 2000, truth.delay);
					await this.waiting(truth.delay);
				}
			} else {
				let detail = require(`../assets/four_noble_truths/${truth.english}/detail.txt`);
				await this.push_new_message(detail.default, true, [], 2000, truth.delay);
			}
			// 詢問係咪仲想了解其他諦
			return truth.english === 'path' ? this.know_more_about_other_path_detail() : this.know_more_about_other_truths(true);
		},
		async know_more_about_other_truths(more = false) {
			let chips = this.four_noble_truths.filter(e => e.chinese !== this.question).map(e => e.chinese);
			this.question = 'four_noble_truths';
			this.confirm = false;
			await this.push_new_message(`你${more ? '仲' : ''}想了解多啲邊一個諦呢？`, true, chips, 3000, 2000);
		},
		async tell_noble_eightfold_path_detail(path) {
			// 從txt檔案抽出八正道的詳細內容
			let detail = require(`../assets/four_noble_truths/path/noble_eightfold_path/${path.english}.txt`);
			await this.push_new_message(detail.default, true, [], 1000, path.delay);
			// 詢問係咪仲想了解其他道
			return this.know_more_about_other_path_detail(true);
		},
		async know_more_about_other_path_detail(more = false) {
			let chips = this.noble_eightfold_path.filter(e => e.chinese !== this.question).map(e => e.chinese);
			chips.push('返回');
			this.question = 'noble_eightfold_path';
			this.confirm = false;
			await this.push_new_message(`你${more ? '仲' : ''}想了解多啲八正道入面嘅邊一個呢？`, true, chips, 3000, 2000);
		},
		async check_contain_self_words() {
			let match = false;
			// 檢查問題內是否含有自身相關的字詞，如有則介紹無我
			this.self_words.forEach(e => {
				if (this.question.includes(e)) {
					match = true;
				}
			});
			if (match) {
				return this.ask_for_confirm('anatta');
			}
			// 詢問係咪想了解五藴或整個無我的理論
			return this.know_more_about_anatta();
		},
		check_contain_modern_practice_words() {
			let matches = 0;
			// 檢查問題內是否含有現代實踐相關的字詞(家庭&&社會&&關係)
			this.modern_practice_words.forEach(w => {
				if (this.question.match(w)) {
					matches++;
				}
			});
			return matches === 3;
		},
		async tell_anatta_summary() {
			// 從txt檔案抽出無我的簡介
			let text = require("../assets/anatta/summary.txt");
			await this.push_new_message(text.default, true, [], 1000, 2000);
			// 詢問係咪想了解五藴或整個無我的理論
			return this.know_more_about_anatta(false, true);
		},
		async tell_practice_summary() {
			// 從txt檔案抽出現代實踐內家庭社會關係的簡介
			let text = require(`../assets/practice/family.txt`);
			await this.push_new_message(text.default, true, [], 1000, 1000);
			await this.waiting(2000);
			// 詢問係咪想了解多啲修習
			return this.know_more_about_practice();
		},
		async tell_study_summary() {
			// 從txt檔案抽出修習的簡介
			let summary = require("../assets/study/summary.txt");
			await this.push_new_message(summary.default, true, [], 1000, 1000);
			await this.waiting(2000);
			// 詢問係咪想了解多啲修習
			return this.ask_for_confirm('study');
		},
		async tell_study_detail() {
			// 從txt檔案抽出修習的詳細內容
			for (let e of [1, 2]) {
				let detail = require(`../assets/study/detail${e}.txt`);
				await this.push_new_message(detail.default, true, [], 1000, 2000);
				await this.waiting(2000);
			}
			this.question = null;
			this.confirm = false;
			await this.waiting(1000);
			return this.ask_again();
		},
		async know_more_about_anatta(more = false, deep = false) {
			let chips = ['五藴', '無我'];
			let message = `你想了解一下五藴，定想${deep?'再':''}深入了解一下無我呢？`;
			if (more) {
				chips = this.anatta.filter(e => e.chinese !== this.question).map(e => e.chinese);
				message = '你仲想了解啲咩呢？';
			}
			chips.push('無興趣');
			this.question = 'anatta';
			this.confirm = false;
			return this.push_new_message(message, true, chips, 2000, 2000);
		},
		async know_more_about_practice(more = false) {
			let chips = ['現生得樂之道', '身後四德'];
			let message = `你想唔想了解一下以下嘅理論呢？`;
			if (more) {
				chips = this.practice.filter(e => e.chinese !== this.question).map(e => e.chinese);
				message = '你仲想了解啲咩呢？';
			}
			chips.push('無興趣');
			this.question = 'practice';
			this.confirm = false;
			return this.push_new_message(message, true, chips, 2000, 2000);
		},
		async tell_anatta_detail(a) {
			if (a.english === 'skandha') {
				// 如五藴，分開2個對話欄
				for (let e of [1, 2]) {
					let detail = require(`../assets/anatta/skandha${e}.txt`);
					await this.push_new_message(detail.default, true, [], 500, a.delay);
					await this.waiting(a.delay);
				}
			} else {
				let detail = require(`../assets/anatta/${a.english}.txt`);
				await this.push_new_message(detail.default, true, [], 1000, a.delay);
			}
			// 詢問係咪仲想了解其他
			return this.know_more_about_anatta(true);
		},
		async tell_practice_detail(p) {
			let detail = require(`../assets/practice/${p.english}.txt`);
			await this.push_new_message(detail.default, true, [], 1000, p.delay);
			// 詢問係咪仲想了解其他
			return this.know_more_about_practice(true);
		},
		async push_new_message(text, buddha = false, chips = [], delay = 0, ms = 0, train_text = null) {
			let id = this._uuid();
			let new_message = {
				id: id,
				buddha: buddha,
				text: text,
				chips: chips,
				time: moment().format('HH:mm'),
				delay: delay,
				ms: ms,
				train_text: train_text,
			};

			// 發送訊息
			return buddha ? this.buddha_queue.push(new_message) : this.push_conversation(new_message);
		},
		async push_conversation(new_message) {
			// 扮打字特效
			if (new_message.buddha) {
				await this.show_typing_effect(new_message.delay, new_message.ms)
			}
			this.conversation.push(new_message);
			await this.$nextTick();
			// 將畫面scroll到最新的信息 i.e.底部
			let el = document.getElementById(new_message.id);
			return el.scrollIntoView({behavior: "smooth"});
		},
		async train_text(text, label = 'pos') {
			try{
				let response = await $.post(`${api_url}/train`, {
					text: text,
					label: label,
				});
				console.log(response);
			} catch (e){
				console.log(e);
			}
		}
	},
}
</script>

